import React from 'react';

const SapFiFaqSection = () => {
  return (
    <section className="course-list_course_faq_block__wqrNc py-10 lg:py-40 text-primary-100">
      <div className="container mx-auto px-4">
        <div className="faq faq--blue">
          <div className="header mb-12">
            <h2 className="uppercase font-source text-sm mb-6 tracking-[1.393px] text-[#002169] text-center">
              Frequently Asked Questions
            </h2>
            <h3 className="text-[#0A044B] text-5xl leading-[64px] font-semibold text-center mb-10">
              SAP FI: Frequently Asked Questions
            </h3>
            <p className="text-[#4A5568] text-lg leading-[28px] text-center mb-10">
              Explore commonly asked questions about our SAP FI course. Get answers on topics like financial accounting, reporting, and system integration to kickstart your journey in SAP.
            </p>
          </div>
          <div data-accordion-component="Accordion" className="accordion space-y-8 lg:mx-12 mobile:mx-0">
            <AccordionItem
              id="accordion__heading-1"
              title="Is SAP FI suitable for beginners?"
              content="Yes, SAP FI is suitable for beginners with a basic understanding of accounting principles. The course provides a comprehensive introduction to SAP FI and hands-on training."
            />
            <AccordionItem
              id="accordion__heading-2"
              title="Do I need any prior experience to enroll in the SAP FI course?"
              content="No prior experience is required to enroll in the SAP FI course. However, familiarity with basic accounting concepts can be beneficial."
            />
            <AccordionItem
              id="accordion__heading-3"
              title="What are the job opportunities after completing the SAP FI course?"
              content="After completing the SAP FI course, you can pursue careers as an SAP FI consultant, financial analyst, accounts manager, or finance officer in various industries."
            />
            <AccordionItem
              id="accordion__heading-4"
              title="What type of certificate is awarded upon finishing the course?"
              content="A globally recognized SAP end user certificate is awarded after successful completion and passing the certification examination."
            />
            <AccordionItem
              id="accordion__heading-5"
              title="How long does it take to complete the SAP FI course?"
              content="The duration of the SAP FI course varies based on the learning path chosen. Typically, it ranges from a few weeks to several months, including practical training."
            />
            <AccordionItem
              id="accordion__heading-6"
              title="What learning materials are provided with the SAP FI course?"
              content="Participants receive comprehensive study materials, including textbooks, online resources, and access to SAP FI software for hands-on practice."
            />
            <AccordionItem
              id="accordion__heading-7"
              title="Can I access the SAP FI course content online?"
              content="Yes, the SAP FI course content is available online through our learning management system (LMS). Participants can access lectures, assignments, and discussions from anywhere."
            />
            <AccordionItem
              id="accordion__heading-8"
              title="Is the SAP FI course instructor-led or self-paced?"
              content="The SAP FI course offers both instructor-led and self-paced learning options. Participants can choose the mode that suits their schedule."
            />
            <AccordionItem
              id="accordion__heading-9"
              title="What are the prerequisites for enrolling in the SAP FI course?"
              content="There are no specific prerequisites for the SAP FI course. Basic computer skills and an interest in finance will facilitate learning."
            />
            <AccordionItem
              id="accordion__heading-10"
              title="How can SAP FI certification benefit my career?"
              content="SAP FI certification enhances your credibility as a skilled professional in financial accounting, opening doors to lucrative job opportunities and career advancement."
            />
          </div>
        </div>
      </div>
    </section>
  );
};

const AccordionItem = ({ id, title, content }) => {
  const [isOpen, setIsOpen] = React.useState(false);

  const toggleAccordion = () => {
    setIsOpen(!isOpen);
  };

  return (
    <div className="border-b border-[#E4E7EC]">
      <div className="accordion__heading px-4 py-3 cursor-pointer flex justify-between items-center" onClick={toggleAccordion}>
        <label className="font-semibold">{title}</label>
        <svg
          className={`w-6 h-6 transition-transform transform ${isOpen ? 'rotate-180' : 'rotate-0'}`}
          xmlns="http://www.w3.org/2000/svg"
          viewBox="0 0 24 24"
          strokeWidth="1.5"
          strokeLinecap="round"
          strokeLinejoin="round"
          fill="none"
          stroke="currentColor"
        >
          <path stroke="none" d="M0 0h24v24H0z" fill="none" />
          <polyline points="6 9 12 15 18 9" />
        </svg>
      </div>
      <div className={`accordion__panel ${isOpen ? 'block' : 'hidden'}`}>
        <div className="px-4 py-3">
          <p className="text-sm">{content}</p>
          <a
            href=""
            className="btn btn--border py-3 px-6 font-manrope inline-block mt-3"
          >
            Know more
          </a>
        </div>
      </div>
    </div>
  );
};

export default SapFiFaqSection;
